import '../../common/Modal.css'

import { Trans } from '@lingui/react'
import { addMinutes, parse } from 'date-fns'
import { observer } from 'mobx-react-lite'
import React, { useRef } from 'react'
import { useQuery } from 'react-apollo-hooks'
import { Container, Modal, ModalBody, ModalHeader } from 'reactstrap'

import { PrimaryButton } from '../../common/Buttons'
import { useRoot } from '../../common/WithRoot'
import { GOrderTimePicker } from '../../graph/types'
import { parseTime } from '../../helpers/formats'
import { RM } from '../../helpers/ramda'
import { i18n } from '../../i18n'
import { TimePicker } from '../timePicker/TimePicker'
import { OrderTimePickerQuery } from './OrderTimePicker.graphql'

export const OrderTimePicker = observer(() => {
  const { order } = useRoot()

  const { data, loading } = useQuery<
    GOrderTimePicker.Query,
    GOrderTimePicker.Variables
  >(OrderTimePickerQuery, {
    variables: { branchId: order.branchId! },
    suspend: false,
    skip: !order.hasOrder,
  })

  const selectedTime = useRef<Date | undefined>(order.deliverAt)
  const onConfirmAndClose = async () => {
    order.setDeliverAt(selectedTime.current)
    order.closeTimePicker()
  }

  const getStartTime = () => {
    const { companyBranch } = data!
    let minutesOffset = 0
    if (order.usingDeliveryOrderMethod) {
      minutesOffset = companyBranch.defaultDeliveryTime
    }
    if (order.usingPickupOrderMethod) {
      minutesOffset = companyBranch.defaultPickupTime
    }
    const openAt =
      companyBranch.isOpen || !companyBranch.willOpenAt
        ? new Date()
        : new Date(companyBranch.willOpenAt)
    return addMinutes(openAt, minutesOffset)
  }

  const getEndTime = () => {
    const { companyBranch } = data!
    if (companyBranch.isOpen && companyBranch.willCloseAt) {
      return new Date(companyBranch.willCloseAt)
    }
    // if closed, grab the last returned business hour closing time
    const lastBusinessHour = RM.last(companyBranch.businessHours)
    if (!lastBusinessHour || lastBusinessHour.closed) {
      return null
    }
    return parse(lastBusinessHour.closeAt, 'HH:mm', new Date())
  }

  const getFilterIntervals = (): Interval[] => {
    const { companyBranch } = data!
    const now = new Date()
    return companyBranch.businessHours
      .filter(RM.propEq('closed', true))
      .map(bhour => ({
        start: parseTime(bhour.openAt, now),
        end: parseTime(bhour.closeAt, now),
      }))
  }

  const renderModalContent = () => {
    if (loading || !data) {
      return null
    }

    const maxDate = getEndTime()
    if (maxDate === null) {
      return (
        <Trans>
          It's not possible to pick a specific order time at this moment.
        </Trans>
      )
    }

    return (
      <TimePicker
        minuteStep={15}
        minDate={getStartTime()}
        maxDate={maxDate}
        initialTime={selectedTime.current}
        filterIntervals={getFilterIntervals()}
        onStepChange={time => (selectedTime.current = time)}
      />
    )
  }

  const header = order.usingDeliveryOrderMethod
    ? i18n.t`Deliver at what time?`
    : i18n.t`When you will pickup the order?`

  return (
    <Modal
      isOpen={order.timePickerOpen}
      toggle={order.closeTimePicker}
      className="modal__container"
      autoFocus={true}
      backdrop="static"
      fade={false}
    >
      <ModalHeader
        toggle={order.closeTimePicker}
        tag={Container}
        className="modal__header"
      >
        <span>{header}</span>
      </ModalHeader>
      <ModalBody className="modal__body">
        <div className="modal__body__content">{renderModalContent()}</div>
        <div className="modal__body__button__line">
          <div className="modal__body__button__strip" />
          <PrimaryButton
            onClick={onConfirmAndClose}
            size="md"
            className="modal__body__button"
          >
            {i18n.t`Confirm time`.toLocaleUpperCase()}
          </PrimaryButton>
        </div>
      </ModalBody>
    </Modal>
  )
})
