import { Plural, Trans } from '@lingui/react'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components'

import { ShopStatus } from '../../common/ShopStatus'
import { useRoot } from '../../common/WithRoot'
import { GCompanyBranchStatus } from '../../graph/types'

interface IProps {
  status?: GCompanyBranchStatus.Fragment
  deliveryTime: number
  pickupTime: number
}

const StatusDisplayOpen = styled.span`
  font-style: italic;
  color: #0f9849;
`

const StatusDisplayClosed = styled.span`
  font-style: italic;
  color: #b40000;
`

export const ShopStatusDisplay: React.FC<IProps> = ({
  status,
  deliveryTime,
  pickupTime,
}) => {
  const { order } = useRoot()
  if (!status) {
    return null
  }
  return useObserver(() => (
    <ShopStatus
      status={status}
      render={s => {
        switch (s.decision) {
          case 'Open':
            return (
              <StatusDisplayOpen data-testid="statusDisplayOpen">
                {order.usingDeliveryOrderMethod ? (
                  <Trans>We are delivering in {deliveryTime} minutes</Trans>
                ) : (
                  <Trans>
                    We can prepare your order in {pickupTime} minutes
                  </Trans>
                )}
              </StatusDisplayOpen>
            )
          case 'OpeningSoon':
            return (
              <StatusDisplayClosed>
                <Plural
                  value={s.openingIn}
                  one="Opening in 1 minute"
                  other="Opening in # minutes"
                />
              </StatusDisplayClosed>
            )
          case 'ClosingSoon':
            return (
              <StatusDisplayOpen>
                <Plural
                  value={s.closingIn}
                  one="Closing in 1 minute"
                  other="Closing in # minutes"
                />
              </StatusDisplayOpen>
            )
          case 'Closed':
            return (
              <StatusDisplayClosed>
                <Trans>Closed</Trans>
              </StatusDisplayClosed>
            )
        }
      }}
    />
  ))
}
