import gql from 'graphql-tag'

export const OrderTimePickerQuery = gql`
  query GOrderTimePicker($branchId: ID!) {
    companyBranch(id: $branchId) {
      id
      isOpen
      businessHours {
        id
        closeAt
        openAt
        closed
      }
      defaultDeliveryTime
      defaultPickupTime
      willOpenAt
      willCloseAt
    }
  }
`
